export class BpmBean {
    // productCategoryId:string;   //  基础产品ID
    productGroup: string;          //			产品部件描述
    productName: string;          //			产品部件名称
    description: string;          //产品部件id
    productTypeId: string;          //			产品部件名称
    feature: any;          //			产品部件名称
    productFeature: any;          //			产品部件名称
    productId: string;          //			基础产品ID
    functionList: any;
    functionContentList: any;//功能内容列表 
    productGroupId: string;
    productLineId: string;
}
export class ComponentViewBean {
    productFeatureCategoryId: string;            //部件Id
    description: string;          //部件名称     
    pageNum: number = 1;            //当前页
    pageSize: number = 10;           //每页显示数量
    productCategoryLineId: string;          //			产品线Id	String	Y
    productCategoryGroupId: string;          //			产品组Id	String	Y
    productCategoryId: string;          //			基础产品Id	String	Y
    productId: string;          //			可售产品Id	String	Y
}


// 功能分页
export class ProductFunctionBean {
    productFunctionId: string = '';//功能编号
    productFunctionName: string = '';//功能名称
    pageNum: number = 1;            //当前页
    pageSize: number = 10;           //每页显示数量
}


