
import { ActivatedRoute, Params, ParamMap } from '@angular/router';
import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { BasicVBean } from './../bean/base-product-view.bean';
import { Message } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import * as _ from 'lodash';

@Component({
  selector: 'app-base-view-details',
  templateUrl: './base-view-details.component.html',
  styleUrls: ['./base-view-details.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
/**
 * 基础产品详情
 */
export class BaseViewDetailsComponent implements OnInit, OnChanges {
  tabMesg: any = tableMessage; // 列表无数据
  @Input() inValue: string;

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  // 实体
  basicVBean: BasicVBean = new BasicVBean();
  // 列表
  cars_2: any;
  cars_3: any;
  cars_4: any;
  list: any = [];
  // 提示信息
  msgs: Message[] = [];
  zujian_ist1: any = [];
  shuxing_ist2: any = [];
  aIndex: number = 0;
  i: number;
  cars_right: any = []; // 属性列表
  cars_attrbute: any = []; // 属性列表
  showClass: string; // 点击组件
  shows: string;
  shows1: string;
  arrrs: any = [];
  functionTypeV: any; // 功能类型
  scheduleId: string;
  taskCategoryId: string;
  configLevelV: any; // 属性配置级别
  code: any;

  param: any;
  statusButton: boolean;
  paymentMethodTypeList: any = [];
  productContentList: any = [];
  resultListAllse: any = [
    { 'label': '请选择', 'value': '' },
  ];
  QuestionLists: any = [];
  isMybankCust: any = []; // 是否必输
  vlaueOpenf: string = 'open';
  isMybank: any = [];
  constructor(
    public productManageHttpService: ProductManageHttpService,
    public route: ActivatedRoute,
    public commfunc: Commfunc,
  ) {
    this.codeValues();  // 调用方法，获取全部码值
    this.configLevelV = this.code['configLevel'];
    this.functionTypeV = this.code['functionTypes'];
    this.isMybank = this.code['isMybankCust'];
    this.isMybankCust = this.isMybank.splice(1);
  }
  ngOnInit() {
    if (!this.inValue) {
      this.queryEvent();
    }
    this.queryContentType();
    this.queryQuestionType();
  }
  ngOnChanges() {
    this.queryEvent();
  }
  // 码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret; // code是自己声明的变量
  }

  // 交付服务类型下拉列表查询
  queryContentType() {
    this.productManageHttpService.queryContentType({}).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.resultListAllse = [
          { 'label': '请选择', 'value': '' },
        ];
        let obj = data.contentTypeMap;
        for (var key in obj) {
          this.resultListAllse.push({
            label: obj[key],
            value: key,
          });
        }
        this.resultListAllse = _.clone(this.resultListAllse);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }
  // 属性值类型
  queryQuestionType() {
    this.productManageHttpService.queryQuestionType({}).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.QuestionLists = [];
        let obj = data.questionTypeMap;
        for (var key in obj) {
          this.QuestionLists.push({
            label: obj[key],
            value: key,
          });
        }
        this.QuestionLists = _.clone(this.QuestionLists);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  // 关闭
  closeClick() {
    this.outValue.emit(false);
  }
  queryEvent() {
    if (!this.inValue) {
      this.param = {
        baseProductId: this.route.snapshot.params['baseProductId'],
        status: this.route.snapshot.params['status']
      };
    } else {
      this.param = { baseProductId: this.inValue };
    }
    // 组件属性查询
    this.productManageHttpService.getBaseProductDetails(this.param).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.list = data.productCategory; // 详情展示
        let created = new Date(this.list.createdDate);
        this.list.createdDate = this.commfunc.transDateN(created);
        let lastModified = new Date(this.list.lastModifiedDate);
        this.list.lastModifiedDate = this.commfunc.transDateN(lastModified);
        this.cars_2 = data.productFunctionList; // 功能查询
        this.zujian_ist1 = data.productFeatureCategoryList; // 组件展示
        this.shuxing_ist2 = data.productFeatureCategoryList[0].productFeatureTypeList;//属性展示
        this.dianji(0, this.zujian_ist1[0]);
        this.productContentList = data.productContentList;
        this.productContentList.forEach((items, i) => {
          items.contentList.forEach((itemse, k) => {
            let fromd = new Date(itemse.fromDate);
            this.productContentList[i].contentList[k].fromDate = this.commfunc.transDateN(fromd);
            if (itemse.thruDate) {
              let thrud = new Date(itemse.thruDate);
              this.productContentList[i].contentList[k].thruDate = this.commfunc.transDateN(thrud);
            } else {
              this.productContentList[i].contentList[k].thruDate = '';
            }
            // let thrud = new Date(itemse.thruDate);
            // this.productContentList[i].contentList[k].thruDate = this.commfunc.transDateN(thrud);
          });
        });
        this.productContentList = _.clone(this.productContentList);
        this.resultfClick(0, this.productContentList[0], 'pay');
        this.resultfClick(0, this.productContentList[0], 'survey');
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  dianji(i, item) {
    this.showClass = item.productFeatureCategoryName;
    let arrayN = [];
    this.aIndex = i;
    this.shuxing_ist2 = this.zujian_ist1[i].productFeatureTypeList;
    this.shuxing_ist2.forEach(item => {
      if (item.featureType == '01') {
        item.shuxingType = 1;
        item.productFeatureList.forEach(items => {
          arrayN.push(items.productFeatureDesc);
        });
        item.shuxingmiaoshu = arrayN.join(",");
        arrayN = [];
      }
      if (item.featureType == '02') {
        item.shuxingType = 2;
        item.productFeatureList.forEach(items => {
          if (items.valueType == '05') {
            item.shuxingType = 3;
            item.def = items.def;
          } else if (items.valueType == '06') {
            if (item.configLevel == "0") {
              item.shuxingType = 61;
              item.def = items.def;
            } else {
              item.shuxingType = 62;
              item.min = items.min;
              item.max = items.max;
            }
          } else {
            item.min = items.min;
            item.max = items.max;
          }
        });
      }
    });
  }
  //  //点击组件属性查看
  resultfClick(i, col, item) {
    // 回显
    if (item == 'pay') {
      this.shows = col.functionName;
      this.paymentMethodTypeList = col.paymentMethodTypeList;
      this.cars_3 = col.contentList;
      this.paymentMethodTypeList = _.clone(this.paymentMethodTypeList);
      this.cars_3 = _.clone(this.cars_3);
    } else if (item == 'survey') {
      this.shows1 = col.functionName;
      this.cars_4 = col.surveyOptionList[0].questonList;
      this.cars_4 = _.clone(this.cars_4);
    }
  }
}
