import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { ChooseCopyAdd } from './bean/choose-copy-add.bean';
import { TreeNode } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'app-choose-copy-adds',
  templateUrl: './choose-copy-adds.component.html',
  styleUrls: ['./choose-copy-adds.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
export class ChooseCopyAddsComponent implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any;

  head_title: string = '全局目录';
  //提示信息
  msgs: Message[] = [];
  selectedFiles: TreeNode[];
  //列表
  cars_2: any;
  treeData: any;
  listData: any = {};

  //模态框
  showModel: string;
  headerTitle: string
  display: boolean = false;

  //组件的参数
  updateValue: any = [];
  chooseCopyAdd: ChooseCopyAdd = new ChooseCopyAdd();
  resultList: any[];
  constructor(
    public productManageHttpService: ProductManageHttpService,
    public commfunc: Commfunc) { }

  ngOnInit() {
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }
    this.query_line();
  }
  query_line() {
    this.productManageHttpService.productCategoryLineQueryList(this.chooseCopyAdd).subscribe(data => {
      if (data.returnCode.code == "success") {
        let tempArr = [];
        data.productLineList.forEach(item => {
          let tempObj = { id: 'line' };
          tempObj['label'] = item['productLineName'];
          tempObj['value'] = item;
          tempObj['children'] = [{}];
          tempArr.push(tempObj)
        });
        this.treeData = tempArr;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  nodeCheck(event) {

    if (event.node.id == "jichu") {
      let param = {
        productTemplateId: event.node.value.tplId
      }
      let arr = [];
      this.productManageHttpService.findAvailableProductList(param).subscribe(data => {
        if (data.returnCode.code == "success") {
          this.cars_2 = data.resultList;
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      })
      let params = {
        productId: event.node.value.tplId
      }
      this.productManageHttpService.getBaseProduct(params).subscribe(data => {
        if (data.returnCode.code == "success") {
          this.listData = data.resultList[0];
          this.resultList = data.resultList;
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      })
    }
  }
  //+号点击
  nodeExpend(event) {
    if (event.node.id == "line") {
      let parmData1 = {
        pageNum: 1,
        pageSize: 10000,
        productLineId: event.node.value.productLineId,
      }
      let arr = [];
      this.productManageHttpService.productCategoryGroupInfoQueryList(parmData1).subscribe(data => {
        if (data.returnCode.code == "success") {
          data.productGroupInfoList.forEach(item => {
            let tempObj = { id: 'group' };
            tempObj['label'] = item['groupName'];
            tempObj['value'] = item;
            tempObj['children'] = [{}];
            arr.push(tempObj)
          });
          event.node.children = arr
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      })
    }
    if (event.node.id == "group") {
      let param = {
        groupId: event.node.value.groupId
      }
      let arr = [];
      this.productManageHttpService.pdGroupTemplateQuery(param).subscribe(data => {
        if (data.returnCode.code == "success") {
          data.groupTempList.forEach(item => {
            let tempObj = { id: 'jichu' };
            tempObj['label'] = item['tplName'];
            tempObj['value'] = item;
            tempObj['children'] = [{}];
            arr.push(tempObj)
          });
          event.node.children = arr
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      })
    }
    if (event.node.id == "jichu") {
      let param = {
        productTemplateId: event.node.value.tplId
      }
      let arr = [];
      event.node.children = arr
    }
  }

  // 详情
  basicDetails(item) {
    this.display = true;
    this.headerTitle = '可售产品详情';
    this.showModel = '1';
    this.updateValue = item.productId;
  }

  detailsCall(param) {
    this.display = param;
  }
  serve() {
    if (!this.resultList || this.resultList.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择基础产品！' });
      return;
    } else {
      let parm = {
        display: false,
        list: this.listData
      }
      this.outValue.emit(parm);
    }
  }
}