import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { TreeNode } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import * as _ from 'lodash';
import { FormBuilder, Validators, FormControl, FormGroup } from '@angular/forms';

@Component({
    selector: 'app-surveyApplTypeList-add',
    templateUrl: './surveyApplTypeList-adds.component.html',
    styleUrls: [],
    providers: [ProductManageHttpService, Commfunc]
})
export class SurveyApplTypeListAddsComponent implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    @Input() inValue: any = [];
    // 提示信息
    msgs: Message[] = [];
    surveyOptionType: string; // 调查项值类型
    QuestionLists: any = [
        {
            label: '请选择',
            value: ''
        },
    ]; // 调查项值类型列表
    surveyOptionName: string; // 调查项名称
    surveyOptionDesc: string; // 调查项描述
    // 模态框
    display: boolean = false;
    pageName: string = '';
    requiredField: string = '';
    pageNum: string = '';
    code: any;
    configLevel: any; //  属性配置级别
    isMybankCust: any = []; // 是否必输
    userformb: FormGroup; // 校验

    constructor(
        public productManageHttpService: ProductManageHttpService,
        public commfunc: Commfunc,
        public fb: FormBuilder
    ) {
        this.codeValues(); // 调用方法，获取全部码值
        this.isMybankCust = this.code['isMybankCust'];
        this.userformb = fb.group({
            'pageNum': new FormControl('', [Validators.pattern(/^[1-9]\d*$/)]),
            'surveyOptionType': ['', Validators.required],
            'surveyOptionName': new FormControl('', [Validators.pattern(/^\S+$/), Validators.required]),
            'surveyOptionDesc': ['', Validators.required],
            'requiredField': [''],
        });
    }

    ngOnInit() {
        this.queryQuestionType();
    }
    // 码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret; // code是自己声明的变量
    }
    // 属性值类型
    queryQuestionType() {
        this.productManageHttpService.queryQuestionType({}).subscribe(data => {
            if (data.returnCode.code == "success") {
                let obj = data.questionTypeMap;
                for (var key in obj) {
                    this.QuestionLists.push({
                        label: obj[key],
                        value: key,
                    });
                }
                this.QuestionLists = _.clone(this.QuestionLists);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        });
    }
    // 完成
    serve() {
        // 验证
        for (const i in this.userformb.controls) {
            this.userformb.controls[i].markAsDirty();
        }
        // 处理是否提交
        for (const i in this.userformb.controls) {
            if (!this.userformb.controls[i].valid) {
                return;
            }
        }
        
        if(this.pageNum){
            let pageNums = Number(this.pageNum);
            if(pageNums>10){
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "属性分页不可大于10！" });
                return;
            }
        }
        let checks = 0;
        if (this.inValue && this.inValue.length > 0) {
            this.inValue.forEach(item => {
                if (item.surveyOptionName === this.surveyOptionName) {
                    checks++;
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "属性名称已存在!" });
                }
            });
        } else {
            this.inValue = [];
        }
        if (checks === 0) {
            this.inValue.push({
                surveyOptionType: this.surveyOptionType,
                surveyOptionName: this.surveyOptionName,
                surveyOptionDesc: this.surveyOptionDesc,
                pageNum: this.pageNum,
                requiredField: this.requiredField,
            });
            this.inValue = _.clone(this.inValue);
            console.log(this.inValue);
            let parm = {
                display: false,
                list: this.inValue,
            };
            this.outValue.emit(parm);
        }
    }
    canel() {
        let parm = {
            display: false,
        };
        this.outValue.emit(parm);
    }
}