  // 组件右移选中
  rightMovre() {
    let stu = true;
    let temp = [];
    let temp1 = [];
    let arrsright = [];
    this.valueListOne.forEach((item, i) => {
      this.cars_right.forEach(item_2 => {
        if (item_2.productFeatureCategoryId == this.cars_left[item].productFeatureCategoryId) {
          stu = false;
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "请不要选择重复组件" });
          this.valueListOne = [];
        }
      });
      if (stu) {
        arrsright.push(item);
        arrsright.sort(function (a, b) { return b - a; });
        this.cars_right.push(this.cars_left[item]);
        if (this.cars_rightlist.length < 10) {
          this.cars_rightlist.push(this.cars_left[item]);
        }
      }
    });
    if (stu) {
      arrsright.forEach((item, i) => {
        this.cars_left.splice(item, 1);
      });
      this.cars_rightlist = _.clone(this.cars_rightlist);
      this.cars_right = _.clone(this.cars_right);
      temp1 = _.clone(this.cars_left);
      this.cars_left = _.clone(temp1);
      this.valueListOne = [];
    }
    if (this.cars_right) {
      this.totals = this.cars_right.length;
    } else {
      this.totals = 0;
    }
    this.checkclick = false;
  }
  // 组件左移选中
  liftMovre() {
    let temp = [];
    let temp1 = [];
    let arrsleft = [];
    this.valueListTwo.forEach(item => {
      arrsleft.push(item);
      arrsleft.sort(function (a, b) { return b - a; });
      this.cars_left.push(this.cars_rightlist[item]);
    });
    arrsleft.forEach((item, i) => {
      this.cars_right.forEach((items, k) => {
        if (items.productFeatureCategoryId === this.cars_rightlist[item].productFeatureCategoryId) {
          this.cars_right.splice(k, 1);
        }
      });
      this.cars_rightlist.splice(item, 1);
    });
    this.cars_right = _.clone(this.cars_right);
    this.cars_rightlist = _.clone(this.cars_rightlist);
    temp1 = _.clone(this.cars_left);
    this.cars_left = _.clone(temp1);
    this.valueListTwo = [];
    if (this.cars_right) {
      this.totals = this.cars_right.length;
    } else {
      this.totals = 0;
    }
    this.checkclicke = false;
  }
