  // 功能右移
  rightFunMovre() {
    let stu = true;
    let temps = [];
    let temps1 = [];
    let arrsright = [];
    this.leftListFun.forEach((item, i) => {
      this.funList_right.forEach(item_2 => {
        if (item_2.productFunctionId == this.funList_left[item].productFunctionId) {
          stu = false;
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "请不要选择重复功能" });
          this.leftListFun = [];
        }
      });
      if (stu) {
        arrsright.push(item);
        arrsright.sort(function (a, b) { return b - a; });
        this.funList_right.push(this.funList_left[item]);
        if (this.funList_rightlist.length < 10) {
          this.funList_rightlist.push(this.funList_left[item]);
        }
      }
    });
    if (stu) {
      arrsright.forEach((item, i) => {
        this.funList_left.splice(item, 1);
      });
      this.funList_right = _.clone(this.funList_right);
      this.funList_rightlist = _.clone(this.funList_rightlist);
      temps1 = _.clone(this.funList_left);
      this.funList_left = _.clone(temps1);
      this.leftListFun = [];
      if (this.funList_right) {
        this.totalg = this.funList_right.length;
      } else {
        this.totalg = 0;
      }
    }
  }
  // 功能左移
  liftFunMovre() {
    let tempss = [];
    let tempss1 = [];
    let arrsleft = [];
    this.rightListFun.forEach(item => {
      arrsleft.push(item);
      arrsleft.sort(function (a, b) { return b - a; });
      this.funList_left.push(this.funList_right[item]);
    });
    arrsleft.forEach((item, i) => {
      this.funList_right.forEach((items, k) => {
        if (items.productFunctionId === this.funList_rightlist[item].productFunctionId) {
          this.funList_right.splice(k, 1);
        }
      });
      this.funList_rightlist.splice(item, 1);
    });
    this.funList_right = _.clone(this.funList_right);
    this.funList_rightlist = _.clone(this.funList_rightlist);
    tempss1 = _.clone(this.funList_left);
    this.funList_left = _.clone(tempss1);
    this.rightListFun = [];
    if (this.funList_right) {
      this.totalg = this.funList_right.length;
    } else {
      this.totalg = 0;
    }
  }
