import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//页面
import { BaseProductViewComponent } from './base-product-view/base-product-view.component';//基础产品配置-基础产品查看
import { BaseProductConfigComponent } from './base-product-config/base-product-config.component';//基础产品配置-基础产品配置
import { BaseProductModifyComponent } from './base-product-modify/base-product-modify.component';//基础产品配置-基础产品修改
const routes: Routes = [
    {
        path: 'base-product-view', component: BaseProductViewComponent,
    },
    {
        path: 'base-product-config', component: BaseProductConfigComponent,
    },
    {
        path: 'base-product-modify', component: BaseProductModifyComponent,
    },
   
];

export const BaseProductRouting: ModuleWithProviders = RouterModule.forChild(routes);
